<template>
    <h4>NameComponent1</h4>

    <fieldset>
        <h4>ref 定義基本數據類型</h4>

        <div class="container">
            <div class="row">
                <div class="col">
                    <h5>數值: {{ sum }}</h5>
                    <button @click="sum++">點我+1</button>
                </div>
                <div class="col">
                    <h5>布林: {{ flag }}</h5>
                    <button @click="flag=!flag">點我反轉</button>
                </div>
            </div>
        </div>

    </fieldset>

    <fieldset>
        <h4>ref 定義對象數據類型</h4>

        <div class="container">
            <div class="row">
                <div class="col">
                    <h5>食物1: {{ foods[0] }}</h5>
                    <ul>
                        <li v-for="(f, index) of foods" :key="index">{{ f }}</li>
                    </ul>
                    <button @click="foods[0]+='!'">修改食物1</button>
                </div>
                <div class="col">
                    <h5>姓名: {{ person.name }}</h5>
                    <h5>年齡: {{ person.age }}</h5>
                    <h5>興趣2: {{ person.hobby[1].name }}</h5>
                    <ul>
                        <li v-for="(h, index) of person.hobby" :key="index">{{ h.name }}</li>
                    </ul>
                    <button @click="person.name+='~'">修改姓名</button>
                    <button @click="person.hobby[1].name='學習'">修改興趣2</button>
                </div>
            </div>
        </div>

    </fieldset>
    
    <fieldset>
        <h4>reactive 定義對象數據類型</h4>

        <div class="container">
            <div class="row">
                <div class="col">
                    <h5>飲料2: {{ drink[1] }}</h5>
                    <ul>
                        <li v-for="(d, index) of drink" :key="index">{{ d }}</li>
                    </ul>
                    <button @click="drink[1]+='!'">修改飲料2</button>
                </div>
                <div class="col">
                    <h5>學生: {{ student.name }}</h5>
                    <h5>年級: {{ student.level }}</h5>
                    <h5>待辦事項1: {{ student.todos[0].name }}</h5>
                    <ul>
                        <li v-for="(t, index) of student.todos" :key="index">{{ t.name }}</li>
                    </ul>
                    <button @click="student.level++">增長年級</button>
                    <button @click="student.todos[0].name='看電視'">修改待辦事項1</button>
                </div>
            </div>
        </div>

    </fieldset>

</template>
<script>
import { reactive, ref } from 'vue'

export default {
    name: 'NameComponent1',
    
    setup() {
        console.log('--- NameComponent1 ---')

        // ref 定義基本數據類型
        let sum = ref(0)
        let flag = ref(true)
        console.log('sum', sum.value)
        console.log('flag', flag.value)


        // ref 定義對象數據類型
        let person = ref({
            name: '張三',
            age: 18,
            hobby: [
                { name: '看漫畫', isFinish: false },
                { name: '打電動', isFinish: false },
            ]
        })
        console.log('person.value.age', person.value.age)
        console.log('person.value.hobby[1].name', person.value.hobby[1].name)

        let foods = ref(['apple', 'orange'])
        console.log('foods.value[0]', foods.value[0])

        // reactive 定義對象數據類型
        let student = reactive({
            name: '李四',
            level: 1,
            todos: [
                { name: '吃飯', isFinish: false },
                { name: '睡覺', isFinish: false },
            ]
        })
        console.log('student.level', student.level)
        console.log('student.todos[0].name', student.todos[0].name)

        let drink = reactive(['可樂', '綠茶'])
        console.log('drink[1]', drink[1])


        return {
            sum,
            flag,

            foods,
            drink,

            person,
            student,
        }
    }
}

</script>